import mongoose from "mongoose";

const productRequestSchema = new mongoose.Schema({
  title: String,
  category: String,
  upvotes: Number,
  status: String,
  description: String,
  comments: [
    {
      content: String,
      user: {
        image: String,
        name: String,
        username: String,
      },
      replies: [
        {
          content: String,
          replyingTo: String,
          user: {
            image: String,
            name: String,
            username: String,
          },
        },
      ],
    },
  ],
  userId: String,
  isSeed: { type: Boolean, default: false },
});

export default mongoose.models.ProductRequest ||
  mongoose.model("ProductRequest", productRequestSchema);
