import { connectDB } from "../../utils/db";
import GuestUser from "../../../models/GuestUser";
import ProductRequest from "../../../models/ProductRequest";
import mongoose from "mongoose";
import requestIp from "request-ip";
import crypto from "crypto";
import { v4 as uuidv4 } from "uuid";

export default async function handler(req, res) {
  await connectDB();

  if (req.method === "POST") {
    try {
      const clientIp = requestIp.getClientIp(req);
      const { userAgent } = req.body;

      let guest = await GuestUser.findOne({ userAgent, ip: clientIp });

      if (!guest) {
        guest = await GuestUser.create({
          publicUserId: crypto.randomUUID(),
          userAgent,
          ip: clientIp,
          hasCopiedProduct: false,
        });
      }

      if (!guest.hasCopiedProduct) {
        const publicProduct = await ProductRequest.find({
          isPublic: true,
          isSeed: true,
        });

        const userProduct = publicProduct.map((i) => ({
          ...i.toObject(),
          _id: new mongoose.Types.ObjectId(),
          user: guest._id,
          isPublic: true,
          isSeed: false,
          publicId: `${i.id}-${uuidv4()}`,
        }));

        await ProductRequest.insertMany(userProduct);

        await GuestUser.findByIdAndUpdate(guest._id, {
          $set: {
            products: userProduct.map((inv) => inv._id),
            hasCopiedProduct: true,
          },
        });
      }

      return res.status(200).json({ publicUserId: guest.publicUserId });
    } catch (err) {
      console.error("Guest API Error:", err);
      return res.status(500).json({ error: "Server error" });
    }
  }

  res.status(405).json({ message: "Method not allowed" });
}
